@import 'vars';

//////////////////////// Global ////////////////////////

html,body	{
	height:100%;
	width:100%;
}

html {
	box-sizing: border-box;
}
*, *:before, *:after {
	box-sizing: inherit;
}

div	{
	position:relative;
}

img	{
	max-width:100%;
}

.test	{
	font-size:72px;
	position:fixed;
	color:blue;
	&.slide-out	{
		color:red;
	}
}



.decoy	{
	pointer-events: none;
}

#main	{
	transition:.4s ease;
}

.is-hidden	{
	opacity:0;
	transition:.4s ease;
}

.waypoint	{
	opacity:0;
	transition:.3s ease;
	position:relative;
	transform: translate3d(0, 40px, 0);
}

.visible	{
	opacity:1;
	transition:.3s ease-in-out;
	transform: translate3d(0, 0, 0);
	&.delay1	{
		transition:.3s .1s ease;
	}
	&.delay2	{
		transition:.3s .2s ease;
	}
	&.delay3	{
		transition:.3s .3s ease;
	}
	&.delay4	{
		transition:.3s .4s ease;
	}
	&.delay5	{
		transition:.3s .5s ease;
	}
	&.delay6	{
		transition:.3s .6s ease;
	}
	&.delay7	{
		transition:.3s .7s ease;
	}
	&.delay8	{
		transition:.3s .8s ease;
	}
}

.group:after {
  content: "";
  display: table;
  clear: both;
}

.fifty	{
	width:100%;
	float:left;
	.panel	{
		width:50%;
		float:left;
		padding: 8% 6% 8% 6%;
	}
}

.line	{
	width:40px;
	height:4px;
	background:$red;
	display:block;
	margin-bottom:80px;
}

.grid	{
	width:100%;
	margin:0 auto;
	padding: 0 $sidepadding;
	max-width:1880px;
	&.standard-padding	{
		padding-top:$singlepadding;
		padding-bottom:$singlepadding;
	}
}

.grid-3	{
	width:33.3%;
	float:left;
}

.grid-6	{
	width:50%;
	float:left;
}

.grid-9	{
	width:66.6%;
	float:left;
}

.button	{
	font-size:14px;
	background:$red;
	clear:both;
	width:24%;
	transition:.1s ease;
    max-width: 360px;
    overflow:hidden;
	a	{
		font-family:$fontbold;
		font-weight:bold;
		color:#fff;
		padding: 26px 0 24px 0;
		text-decoration: none;
		text-transform: uppercase;
		letter-spacing: 2px;
		display:block;
	}
	&.icon	{
		text-align: left;
		padding-left:32px;
		a:hover	{
			&:after	{
				background-position:0 6px;
			}
		}
		a:after	{
			display:block;
			content:'';
		    width: 84px;
			height: 114px;    
		    background: url('../images/download-icon.svg') no-repeat;
		    background-position: 0 -46px;
			transition:.2s ease;
			position:absolute;
			top:0;
			right:0;
			overflow:hidden;
		}
	}
	
	&.on-pink	{
		background:#fff;
		a	{
			color:$red;
		}
		&:hover	{
			background:$red;
			a	{
				color:#fff;
			}
		}
		
	}
	
	&:hover	{
		background:$darkgray;
			&.on-black	{
			background:#fff;
			a	{
				color:$red;
			}
		}
	}
}

//////////////////////// Fonts ////////////////////////

h1,h2,h3,h4,h5,h6,.eyebrow,p	{
	line-height:120%;
}

.h1	{
	font-size:72px;
	color:$red;
	font-family:$fontbold;
	font-weight: bold;
	letter-spacing: -2px;
    margin: 0 auto;
	span	{
		font-family:$fontserif;
		color:$darkgray;
		font-weight:normal;
	}
}

.h2	{
	font-size:48px;
	color:$darkgray;
	font-family:$fontbold;
	font-weight: bold;
	letter-spacing: -1px;
    margin: 0 auto;
    line-height:130%;
}

.h3	{
	font-size:36px;
	color:$darkgray;
	font-family:$fontbold;
	font-weight: bold;
	letter-spacing: -1px;
    margin: 0 auto;
    line-height:130%;
}

.h4	{
	font-size:13px;
	color:$darkgray;
	font-family:$fontbold;
	font-weight: bold;
	letter-spacing: 2px;
	text-transform: uppercase;
    margin: 0 auto;
    line-height:130%;
}

.h6	{
	font-size:13px;
	color:$red;
	font-family:$fontbold;
	font-weight: bold;
	letter-spacing: 2px;
	text-transform: uppercase;
    margin: 0 auto;
    line-height:130%;
}

li	{
	font-size:24px;
	font-family:$fontbold;
	font-weight: bold;
	letter-spacing: -1px;
    margin: 0 auto;
    line-height:130%;
}

p	{
	font-family:$fontbold;
	font-size:16px;
	font-weight:600;
	color:$gray2;
	line-height:140%;
}

.eyebrow	{
	font-size:14px;
	color:$gray;
	font-family:$fontserif;
	text-transform: uppercase;
	letter-spacing: 2px;
	margin-bottom: 30px;
}

a.text-link	{
	font-size:13px;
	font-family:$fontbold;
	font-weight: bold;
	letter-spacing: 2px;
	text-transform: uppercase;
    line-height:130%;
    text-decoration: underline;
    transition:.1s ease;
    color:$red;
    &.on-black	{
	    &:hover	{
		    color:#fff;
	    }
    }
    &:hover	{
	    color:#000;
	    text-decoration: none;
    }
}

//////////////////////// Header ////////////////////////

nav	{
	position:fixed;
	width:100%;
	z-index:11;
}

.logo	{
	background:$red;
	width:100px;
	height:100px;
	text-indent:-9999px;
	position:absolute;
	left:0;
	top:0;
	transition:.1s ease;
	&.open	{
		background:transparent;
	}
	a	{
		width:100px;
		height:100px;
		background:url('../images/logo.svg');
		display: block;
	    background-repeat: no-repeat;
	    background-size: 64%;
	    background-position: 18px 24px;
	}
	&:hover	{
		background:$darkgray2;
	}
}

.m-scene .scene_element {
  -webkit-animation-duration: 0.25s;
  animation-duration: 0.25s;
  -webkit-transition-timing-function: ease-in;
  transition-timing-function: ease-in;
/* this means that before the animation runs, the element will have the starting css rules, then keep the end rules */
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;  }

.m-scene .scene_element--fadeIn {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}

.m-scene.is-exiting .scene_element {
  -webkit-animation-direction: alternate-reverse;
  animation-direction: alternate-reverse;
}

.full-menu	{
	position:fixed;
	background:$red;
	width:0;
	height:100%;
	z-index:-1;
	font-family: $fontbold;
	font-weight:bold;
	transition:.4s ease;
	right:0;
	&.hide-menu	{
		pointer-events: none;
	}
	a	{
		color:#fff;
		text-decoration: none;
	}
	

}

.hide-menu	{
	opacity:0;
	transition:.4s ease;
}

.menu.open	{
	.full-menu	{
		right: 0;
		width:100%;
		&.hide-menu	{
			width:0;
		}
	}
}

.fifty	{
	&.menu-open	{
		height:100%;
		.panel-one	{
			width:100%;
			height: 60%;
		}
		.panel	{
			padding:0 6%;
			height:100%;
			display: flex;
		    justify-content: center;
		    align-items: center;
		}
		.panel:first-child	{
			background:$darkred;
		}
	}
}


.menu-contact-info	{
	height:50%;
	border-top:1px solid #bd2038;
	li	{
	    height:calc(100% / 5);
	    display:table;
	    &:last-child	{
		    
	    }
    }
    a	{
	    width:1%;
	    vertical-align: middle;
	    display: table-cell;
	    top: 5px;
	    position: relative;
	    font-size:18px;
	    transition:.1s ease;
	    &:hover	{
		    color:$darkgray2;
	    }
    }
}

.menu-social	{
	height:50%;
	border-top:1px solid #bd2038;
	li	{
	    height:calc(100% / 4);
	    display:table;
	    float: left;
	    width: 14%;
	    margin-right: 9%;
    }
    a	{
	    width:1%;
	    vertical-align: middle;
	    display: table-cell;
	    position: relative;
	    font-size:18px;
	    transition:.1s ease;
	    &:hover	{
		    color:$darkgray2;
	    }
    }
}

.menu-items	{
	width:100%;
	height: 60%;
	border-top:1px solid #d8233e;
    position: relative;
    li	{
	    height:calc(100% / 6);
	    display:table;
	    border-bottom:1px solid #d8233e;
	    
    }
    a	{
	    width:1%;
	    vertical-align: middle;
	    display: table-cell;
	    font-size:48px;
	    letter-spacing: -1px;
	    top: 5px;
	    position: relative;
	    transition:.1s ease;
	    &:hover	{
		    color:$darkgray2;
	    }
    }
}



