//////////////////////// Case Study Single ////////////////////////

.single-bg	{
	position:fixed;
	background-color:$darkgray;
	width:100%;
	height:100%;
	z-index:1;
	transition:.7s ease;
	&:after	{
		content:'';
		background-image:url('../images/case-studies/Phantom/phantom-bg.jpg');
		background-size:cover;
		position:absolute;
		top:0;
		left:0;
		bottom:0;
		right:0;
		transition:.2s ease;
	}
}

.case-study-hero	{
	padding:18% 0 $singlepadding 0;
	position: relative;
    z-index: 2;
    transition:.4s ease;
    opacity:1;
    .hero-link	{
	    display:inline;
	    color:$red;
	    transition:.1s ease;
	    letter-spacing: -1px;
	    &:hover	{
		    color:#fff;
	    }
    }
	.h1	{
		color:#fff;
	    padding: 3% 0;
	}
	.h3,p,li	{
		color:$gray;
	}
	.h3	{
		color:$red;
		span	{
			color:#fff;
			font-family:$fontserif;
			font-weight:normal;
		}
	}
	p	{
		font-size:22px;
		margin-bottom: 5%;
	}
	a	{
		text-decoration: none;
		padding:2% 0;
		display:block;
		letter-spacing: 1px;
		&.service-link	{
			&:before	{
				width:18px;
				height:22px;
				content:'+';
				display:block;
				float:left;
			}
		}
		&.new-window-icon	{
			&:hover	{
				&:before	{
					background-position:top;
				}
			}
			&:before	{
				width:22px;
				height:22px;
				content:'';
				display:block;
				background:url('../images/new-window-icon.svg');
				background-repeat:no-repeat;
				background-size:100%;
				background-position:bottom;
				transition:0;
				float: left;
			    margin-right: 10px;
			    top: 3px;
			    position: relative;
			}
		}
		&.large	{
			font-size:22px;
			letter-spacing: 0px;
			text-transform: none;
		}
		&:hover	{
			text-decoration: underline;
		}
	}
	li	{
		&:first-child	{
			padding-bottom:2%;
			margin-top:4px;
		}
		font-size:13px;
		letter-spacing: 1px;
		text-transform: uppercase;
	}
	.grid-6	{
		padding: 0 8%;
		&:before	{
			display:block;
			content:'';
			width:40px;
			height:4px;
			background:$gray;
			position: absolute;
		    left: 0;
		    top: 6px;
		}
	}
	
}

.fixed-container	{
	width:100%;
	z-index:2;
	.single-hero-image	{
	    width:100%;
	    height:auto;
	    transition:.4s ease;
	    background-repeat: no-repeat;
	    margin:0 auto;
	}
	.pdf-download	{
		.h2	{
			color:$red;
		}
	}
	.h2	{
		color:#fff;
	}
	ol	{
		margin-left:61px;
	}
	.h6	{
	    font-size: 24px;
	    letter-spacing: 0;
	}
	li	{
		color:#fff;
	    margin-bottom: 30px;
	    letter-spacing: 0;
	    &:last-child	{
		    margin-bottom:0;
	    }
	    span	{
		    color:$red;
	        margin-left: -61px;
		    margin-right: 20px;
	    }
	}
	.hr	{
		display:block;
		height:1px;
		background:$darkgray2;
		width: 100% - ($sidepadding * 2);
	    margin: 0 auto;
	}
	.solution	{
		.h2	{
			color:#fff;
		}
		p	{
			font-size:24px;
			color:#fff;
		}
	}
	.challenge-solution	{
		background: linear-gradient(to bottom, rgba(20, 18, 24, 0) 0%, rgba(20, 18, 24, 0) 1%, #141218 100%);
	}
}

.results	{
	background:#fff;
	float: left;
    width: 100%;
    padding:$standardpadding;
	.h2{
		color:$red;
	}
	li	{
		color:$darkgray;
	}
}

.pagination {
	width:100%;
	float:left;
	.h2{
		color:$darkgray;
		transition:.1s ease;
	}
	.pagination-link	{
		position:absolute;
		z-index:3;
		top:0;
		left:0;
		right:0;
		bottom:0;
		text-indent: -9999px;
	}
	.h4	{
		color:$red;
		margin-bottom:6%;
	}
	
	.panel.one	{
		background:$lightgray;
	}
	.panel.two	{
		background:#fff;
	    position: relative; 
	}
	.panel	{
		transition:.1s ease;
		&:hover	{
			background:$darkgray2;
			transition:.1s ease;
			.h2	{
				color:#fff;
				transition:.1s ease;
			}
		}
	}
}




//////////////////////// Footer ////////////////////////

footer	{
	width:100%;
	float:left;
	background:#201e24;
	position: relative;
	z-index:2;
	padding:$sidepadding;
	a	{
		text-decoration: none;
		color:#fff;
		transition:.1s ease;
		&:hover	{
			text-decoration: underline;
			color:$red;
		}
	}
	img	{
		width:90px;
	}
}

.footer-contact-info	{

    a	{
	    position: relative;
	    font-size:18px;
	    transition:.1s ease;
    }
}

.footer-social	{
	li	{
    }
    a	{
	    position: relative;
	    font-size:18px;
	    transition:.1s ease;
    }
}


